import { FC } from 'react';
import { TeamProps } from '.';
import styles from './Team.module.scss';
import { Container } from '@mui/system';
import Globus from '../../components/Globus/Globus';
import useFetch from '../../hooks';
import { useState, useEffect } from 'react';
import { CircularProgress } from '@mui/material';

export const Team: FC<TeamProps> = (props) => {
	interface result {
		picture: {
			large: string,
			medium: string,
			thumbnail: string
		}
	}

	interface picsResponse {
		results: result[]
	}

	const { data, error, pending } = useFetch<picsResponse>('https://randomuser.me/api/?results=9&inc=picture');
	console.log(data)
	
	return <div className={[styles.Team, props.className].join(' ')} {...props}>
		<Container>
			<div className={styles.bigLogoContainer}>
				<Globus className={styles.globus} />
				<h1 className={styles.bigLogo}></h1>
				<p className={styles.teamDescription}>   -,       ,  ,    .  Monopolist lub,     -  -,      ,               ,      .</p>
			</div>
			<div className={styles.teamMembers}>
				{
					pending ? <CircularProgress /> :
						error ? <h1> </h1> :
							data?.results.map((member: result) => (
								<img className={styles.teamMember} alt='avatar' key={member?.picture?.large} src={member.picture.large} />
							))
				}
			</div>
		</Container>
	</div>;
};
