import { FC } from 'react';
import { OwnerCardProps } from '.';
import styles from './OwnerCard.module.scss';


export const OwnerCard: FC<OwnerCardProps> = (props) => {
	return <div className={[styles.OwnerCard, props.className].join(' ')}>
		<div className={styles.header} style={{ backgroundColor: props.headerColor ? props.headerColor : "#F8941E" }}>
			<h6>
				{ props.smallHeader }
			</h6>
			<h5>
				{ props.bigHeader }
			</h5>
		</div>
		<div className={styles.content}>
			<div><div style={{ width: 80 }} className={styles.placeholder}></div></div>
			<div><div style={{ width: 150 }} className={styles.placeholder}></div></div>
			<div><i style={{background: "green"}} className={['icon-house', styles.house].join(' ')} /><div className={styles.placeholder}></div></div>
			<div><i style={{background: "green"}} className={['icon-house', styles.house].join(' ')} /><div className={styles.placeholder}></div></div>
			<div><i className={['icon-house', styles.house].join(' ')} /><div className={styles.placeholder}></div></div>
			<div><i className={['icon-house', styles.house].join(' ')} /><div className={styles.placeholder}></div></div>
			<div><i style={{background: "red"}} className={['icon-house', styles.house].join(' ')} /><div  style={{ width: 100 }} className={styles.placeholder}></div></div>
		</div>
	</div>;
};
