import { FC } from "react";
import { HeaderProps } from ".";
import styles from "./Header.module.scss";
import { Logo } from "../Logo";
import { Link } from "react-router-dom";
import { Container } from "@mui/system";

export const Header: FC<HeaderProps> = (props) => {
    return (
        <header className={[props.className, styles.header].join(" ")}>
            <Container>
            <nav className={styles.header__nav}>
                <ul className={[styles.header__links, styles.header__links_left].join(" ")}>
                    <li className={styles.header__link}>
                        <Link to={"/"}></Link>
                    </li>
                    <li className={styles.header__link}></li>
                    <li className={styles.header__link}>
                        <Link to={"/team"}></Link>
                    </li>

                    <li className={styles.header__link}>
                        <Link to={"/workshop"}>Workshop</Link>
                    </li>
                </ul>
                <Link to={"/"} className={styles.header__logo}>
                    <Logo />
                </Link>
                <ul className={[styles.header__links, styles.header__links_right].join(" ")}>
                    <li className={styles.header__link}></li>
                    <li className={styles.header__link}>FAQ</li>

                    <li className={styles.header__link}></li>
                    <li className={styles.header__link}></li>
                </ul>
            </nav>
            </Container>
        </header>
    );
};
