import { FC } from "react";
import { Workshop1Props } from ".";
import styles from "./Workshop1.module.scss";
import { SocialLinks } from "../SocialLinks";

export const Workshop1: FC<Workshop1Props> = (props) => {
    return (
        <section className={[styles.Workshop1, props.className].join(" ")} {...props}>
            <article className={styles.content}>
                <img className={styles.bigLogo} src="images/Workshop.png" />
                <p className={styles.description}> </p>
                <SocialLinks />
            </article>
        </section>
    );
};
